@import url(https://fonts.googleapis.com/css2?family=Aladin&family=Lato&display=swap);
html {
  /* To make use of full height of page*/
  min-height: 100%;
  margin: 0;
}

body {
  margin: 0;
  min-height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-size: calc(1em + 6vmin) !important
}

h2 {
  font-size: calc(1em + 2.82vmin) !important
}

h3 {
  font-size: calc(1em + 1.73vmin) !important
}

h4 {
  font-size: calc(1em + 1vmin) !important
}

h5 {
  font-size: calc(1em + 0.45vmin) !important
}

h6 {
  font-size: calc(1em + .27vmin) !important
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Lato', sans-serif;
}

main {
  background-color: #FFCDB2;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.aladin {
  font-family: 'Aladin', cursive;
  font-size: xx-large;
}

.lato {
  font-family: 'Lato', sans-serif
}


@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
